h1 {
  color: red;
  font-size: 20px;
}

html {
  font-size: 62.5%;
  /* box-sizing: border-box; */
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  letter-spacing: 0.1rem;
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
  box-sizing: border-box;
}

::before,
::after {
  box-sizing: border-box;
}

ul {
  list-style: none;
}

.section {
  background: var(--bg-black-900);
  min-height: 100vh;
  display: block;
  padding: 0 3rem;
  opacity: 1;
  position: fixed;
  left: 27rem;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 0.3s ease;
}
.section.back-section {
  z-index: 1;
}
.section.active {
  z-index: 2;
  opacity: 1;
  animation: slideSection 1s ease;
}
.section .container {
  padding-top: 6rem;
  padding-bottom: 7rem;
}

/* Animación del deslisamiento entre secciones */
@keyframes slideSection {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
  }
}
.hidden {
  display: none !important;
}

.padd-15 {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.container {
  max-width: 110rem;
  width: 100%;
  margin: auto;
}

.section-title {
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 6rem;
}
.section-title h2 {
  font-size: 4rem;
  color: var(--text-black-900);
  font-weight: 700;
  position: relative;
}
.section-title h2::before {
  content: "";
  height: 0.4rem;
  width: 5rem;
  background-color: var(--skin-color);
  position: absolute;
  top: 100%;
  left: 0;
}
.section-title h2::after {
  content: "";
  height: 0.4rem;
  width: 2.5rem;
  background-color: var(--skin-color);
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 0.8rem;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.5rem;
  margin-right: -1.5rem;
  position: relative;
}

.btn {
  font-size: 1.6rem;
  font-weight: 500;
  padding: 1.2rem 3.5rem;
  color: white;
  border-radius: 4rem;
  display: inline-block;
  left: 10rem;
  white-space: nowrap;
  border: none;
  background: var(--skin-color);
  transition: all 0.3s ease;
}
.btn:hover {
  transform: scale(1.05);
}

.shadow-dark {
  box-shadow: 0 0 2rem rgba(48, 46, 77, 0.15);
}

body.dark .shadow-dark {
  box-shadow: 0 0 2rem rgba(166, 247, 223, 0.123);
}
body.dark .contact .contact-form .form-item .form-control {
  border: none;
}
body.dark .contact .contact-form .form-item .form-control:focus {
  box-shadow: 0 0 2rem rgba(166, 247, 223, 0.123);
}
body.dark .service .service-item .service-item-inner {
  border: none;
}
body.dark .service .service-item .service-item-inner:hover {
  box-shadow: 0 0 2rem rgba(166, 247, 223, 0.123);
}
body.dark .about .about-content .timeline {
  border: none;
}

h3.hello {
  font-size: 2.8rem;
  margin: 1.5rem 0;
}

h3.hello span {
  font-family: "Stick No Bills", sans-serif;
  letter-spacing: 0.3rem;
  font-size: 3rem;
  font-weight: 700;
  color: var(--skin-color);
}

h3.my-profession {
  font-size: 3rem;
  margin: 1.5rem 0;
}

.typing {
  color: var(--skin-color);
  font-family: "Stick No Bills", sans-serif;
  letter-spacing: 0.3rem;
}

:root {
  --bg-black-900: #faf8f5;
  --bg-black-100: #feffff;
  --bg-black-50: #e8dfec;
  --text-black-900: #302e4d;
  --text-black-700: #504e70;
}

body.dark {
  --bg-black-900: #021011;
  --bg-black-100: #002b2c;
  --bg-black-50: #393939;
  --text-black-900: #fff;
  --text-black-700: #e9e9e9;
}

.aside {
  width: 27rem;
  background: var(--bg-black-100);
  position: fixed;
  left: 0;
  top: 0;
  padding: 3rem;
  height: 100%;
  border-right: 0.1rem solid var(--bg-black-50);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  transition: all 0.3s ease;
}
.aside .logo {
  position: absolute;
  top: 5rem;
  font-size: 3rem;
  text-transform: capitalize;
}
.aside .logo a {
  color: var(--text-black-900);
  font-weight: 700;
  padding: 1.5rem 2rem;
  font-size: 3rem;
  letter-spacing: 0.2rem;
  position: relative;
}
.aside .logo a::before {
  /* estilos del logo, las <randy> */
  content: "";
  position: absolute;
  width: 2rem;
  height: 2rem;
  border-bottom: 0.5rem solid var(--skin-color);
  border-left: 0.5rem solid var(--skin-color);
  bottom: 0;
  left: 0;
}
.aside .logo a::after {
  /* estilos del logo, las <randy> */
  content: "";
  position: absolute;
  width: 2rem;
  height: 2rem;
  border-top: 0.5rem solid var(--skin-color);
  border-right: 0.5rem solid var(--skin-color);
  top: 0;
  right: 0;
}
.aside .logo span {
  font-family: "Domine", serif;
  font-size: 4rem;
}
.aside .nav-toggler {
  height: 4rem;
  width: 4.5rem;
  border: 0.1rem solid var(--bg-black-50);
  cursor: pointer;
  position: fixed;
  left: 30rem;
  top: 2rem;
  border-radius: 0.5rem;
  background: var(--bg-black-100);
  display: none;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}
.aside .nav-toggler span {
  height: 0.2rem;
  width: 1.8rem;
  background: var(--skin-color);
  display: inline-block;
  position: relative;
}
.aside .nav-toggler span::before {
  content: "";
  height: 0.2rem;
  width: 1.8rem;
  background: var(--skin-color);
  position: absolute;
  top: -0.6rem;
  left: 0;
}
.aside .nav-toggler span::after {
  content: "";
  height: 0.2rem;
  width: 1.8rem;
  background: var(--skin-color);
  position: absolute;
  top: 0.6rem;
  left: 0;
}
.aside .nav-toggler.open span {
  background-color: transparent;
}
.aside .nav-toggler.open span::before {
  transform: rotate(45deg);
  top: 0;
}
.aside .nav-toggler.open span::after {
  transform: rotate(-45deg);
  top: 0;
}
.aside .nav {
  position: fixed;
  top: auto;
}
.aside .nav li {
  margin-bottom: 4rem;
  display: block;
}
.aside .nav li a {
  font-size: 1.6rem;
  font-weight: 600;
  display: block;
  border-bottom: 0.1rem solid var(--bg-black-50);
  color: var(--text-black-900);
  padding: 0.5rem 1.5rem;
}
.aside .nav li a.active {
  color: var(--skin-color);
}
.aside .nav li a i {
  margin-right: 1.5rem;
}

.home {
  min-height: 100vh;
  display: flex;
  color: var(--text-black-900);
}
.home .home-info {
  flex: 0 0 60%;
  max-width: 60%;
}
.home .home-info p {
  margin-bottom: 7rem;
  font-size: 2rem;
  color: var(--text-black-700);
}
.home .home-img {
  flex: 0 0 40%;
  max-width: 40%;
  text-align: center;
  position: relative;
}
.home .home-img::after {
  content: "";
  position: absolute;
  height: 8rem;
  width: 8rem;
  border-bottom: 1rem solid var(--skin-color);
  border-right: 1rem solid var(--skin-color);
  right: 0.6rem;
  bottom: -4rem;
}
.home .home-img::before {
  content: "";
  position: absolute;
  height: 8rem;
  width: 8rem;
  border-top: 1rem solid var(--skin-color);
  border-left: 1rem solid var(--skin-color);
  left: 0.6rem;
  top: -4rem;
}
.home .home-img img {
  margin: auto;
  border-radius: 0.5rem;
  height: 40rem;
}

.about .about-content {
  flex: 0 0 100%;
  max-width: 100%;
}
.about .about-content .about-text {
  flex: 0 0 100%;
  max-width: 100%;
}
.about .about-content .about-text h3 {
  font-size: 2.4rem;
  margin-bottom: 1.5rem;
  font-weight: 700;
  color: var(--text-black-900);
}
.about .about-content .about-text h3 span {
  color: var(--skin-color);
}
.about .about-content .about-text p {
  font-size: 1.6rem;
  min-height: 2.5rem;
  color: var(--text-black-700);
}
.about .about-content .personal-info {
  flex: 0 0 60%;
  max-width: 60%;
  margin-top: 4rem;
}
.about .about-content .personal-info .info-item {
  flex: 0 0 50%;
  max-width: 50%;
}
.about .about-content .personal-info .info-item p {
  font-weight: 600;
  padding: 1rem 0;
  font-size: 1.6rem;
  color: var(--text-black-900);
  border-bottom: 0.1rem solid var(--bg-black-50);
}
.about .about-content .personal-info .info-item p span {
  font-weight: 400;
  color: var(--text-black-700);
  margin-left: 0.4rem;
  display: inline-block;
}
.about .about-content .personal-info .buttons {
  margin: 3rem;
}
.about .about-content .personal-info .buttons .btn {
  margin-top: 1rem;
}
.about .about-content .skills {
  flex: 0 0 40%;
  max-width: 40%;
  margin-top: 4rem;
}
.about .about-content .skills .skill-item {
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 2.5rem;
}
.about .about-content .skills .skill-item h5 {
  line-height: 4rem;
  font-weight: 600;
  font-size: 1.6rem;
  color: var(--text-black-900);
  text-transform: capitalize;
}
.about .about-content .skills .skill-item .progress {
  background-color: var(--bg-black-50);
  height: 0.7rem;
  border-radius: 0.4rem;
  width: 100%;
  position: relative;
}
.about .about-content .skills .skill-item .progress .progress-in {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  border-radius: 0.4rem;
  background-color: var(--skin-color);
}
.about .about-content .skills .skill-item .skill-percent {
  position: absolute;
  right: 0;
  color: var(--text-black-900);
  top: -4rem;
  font-weight: 400;
  line-height: 4rem;
}
.about .about-content .education,
.about .about-content .experience {
  flex: 0 0 50%;
  max-width: 50%;
  margin-top: 3rem;
}
.about .about-content h3.title {
  font-size: 2.4rem;
  margin-bottom: 3rem;
  font-weight: 700;
  color: var(--text-black-900);
}
.about .about-content .timeline-box {
  flex: 0 0 100%;
  max-width: 100%;
}
.about .about-content .timeline {
  background-color: var(--bg-black-100);
  padding: 3rem 1.5rem;
  border: 0.1rem solid var(--bg-black-50);
  border-radius: 1rem;
  width: 100%;
  position: relative;
}
.about .about-content .timeline .timeline-item {
  position: relative;
  padding-left: 3.7rem;
  padding-bottom: 5rem;
}
.about .about-content .timeline .timeline-item:last-child {
  padding-bottom: 0;
}
.about .about-content .timeline .timeline-item::before {
  content: "";
  width: 0.1rem;
  position: absolute;
  height: 100%;
  left: 0.7rem;
  top: 0;
  background-color: var(--skin-color);
}
.about .about-content .timeline .circle-dot {
  position: absolute;
  left: 0;
  top: 0;
  height: 1.5rem;
  width: 1.5rem;
  border-radius: 50%;
  background-color: var(--skin-color);
}
.about .about-content .timeline .timeline-date {
  font-weight: 400;
  font-size: 1.4rem;
  margin-bottom: 1.2rem;
  color: var(--text-black-700);
}
.about .about-content .timeline .timeline-date .fa {
  margin-right: 0.5rem;
}
.about .about-content .timeline .timeline-title {
  font-weight: 700;
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  text-transform: capitalize;
  color: var(--text-black-900);
}
.about .about-content .timeline .timeline-text {
  line-height: 2.5rem;
  font-size: 1.6rem;
  text-align: justify;
  color: var(--text-black-700);
}

.service .container {
  padding-bottom: 4rem;
}
.service .service-item {
  margin-bottom: 3rem;
  flex: 0 0 33.33%;
  max-width: 33.33%;
}
.service .service-item .service-item-inner {
  background-color: var(--bg-black-100);
  border: 0.1rem solid var(--bg-black-50);
  border-radius: 1rem;
  padding: 3rem 1.5rem;
  text-align: center;
  transition: all 0.3s ease;
}
.service .service-item .service-item-inner:hover {
  box-shadow: 0 0 2rem rgba(48, 46, 77, 0.15);
}
.service .service-item .service-item-inner:hover .icon {
  background: var(--skin-color);
}
.service .service-item .service-item-inner:hover .icon .fa {
  font-size: 2.5rem;
  color: #fff;
}
.service .service-item .service-item-inner .icon {
  height: 6rem;
  width: 6rem;
  border-radius: 50%;
  display: block;
  margin: 0 auto 3rem;
  text-align: center;
  transition: all 0.3s ease;
}
.service .service-item .service-item-inner .icon .fa {
  font-size: 4rem;
  line-height: 6rem;
  color: var(--skin-color);
  transition: all 0.3s ease;
}
.service .service-item .service-item-inner h4 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: var(--text-black-900);
  font-weight: 700;
  text-transform: capitalize;
}
.service .service-item .service-item-inner p {
  font-size: 1.6rem;
  color: var(--text-black-700);
  line-height: 2.5rem;
}

.portfolio .container {
  padding-bottom: 4rem;
}
.portfolio .portfolio-heading {
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 4rem;
}
.portfolio .portfolio-heading h2 {
  color: var(--text-black-900);
  font-weight: 500;
}
.portfolio .portfolio__section {
  display: grid;
  grid-template-columns: repeat(3, 3fr);
  gap: 1.5rem;
  justify-content: center;
  align-items: start;
}
.portfolio .portfolio__section img {
  width: 100%;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
}
.portfolio .portfolio-item {
  padding: 1rem;
  background-color: var(--bg-black-100);
  overflow: hidden;
  border-radius: 0.5rem;
}
.portfolio .portfolio-item h3 {
  font-size: 1.8rem;
}
.portfolio .portfolio-item .boton__item {
  cursor: pointer;
  color: var(--text-black-900);
  font-weight: 700;
  display: flex;
  align-items: center;
  column-gap: 0.5rem;
}
.portfolio .portfolio-item .boton__item--icon {
  transition: 0.3s;
}
.portfolio .portfolio-item .boton__item:hover .boton__item--icon {
  transform: translateX(0.4rem);
  color: var(--skin-color);
}
.portfolio .portfolio-details {
  display: none;
}
.portfolio .portfolio-details li {
  font-weight: bold;
}
.portfolio .title__item {
  color: var(--skin-color);
  font-size: 1.4rem;
}
.portfolio .text__item {
  color: var(--text-black-700);
  font-weight: 100;
  font-size: 1.4rem;
}
.portfolio .portfolio__popup {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
}
.portfolio .portfolio__popup.open {
  opacity: 1;
  visibility: visible;
}
.portfolio .portfolio__popup-inner {
  background-color: var(--bg-black-100);
  width: 70rem;
  border-radius: 0.5rem;
  padding: 2.5rem;
  position: relative;
}
.portfolio .portfolio__popup-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  font-size: 2.3rem;
  color: var(--skin-color);
  cursor: pointer;
}
.portfolio .portfolio__content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  column-gap: 3rem;
}
.portfolio .portfolio__content img {
  width: 100%;
  border-radius: 0.5rem;
}
.portfolio .portfolio__content .popup-info .subtitle {
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  color: var(--text-black-700);
}
.portfolio .portfolio__content .popup-info .portfolio__body .popup__title {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-black-900);
  margin-bottom: 1rem;
}
.portfolio .portfolio__content .popup-info .portfolio__body .popup__descripcion {
  font-size: 1.3rem;
  color: var(--text-black-700);
  margin-bottom: 1.9rem;
}
.portfolio .portfolio__content .popup-info li {
  margin-bottom: 1rem;
  text-transform: capitalize;
  font-size: 1.3rem;
  color: var(--text-black-900);
}
.portfolio .portfolio__content .popup-info li:last-child {
  margin-bottom: 0;
}
.portfolio .portfolio__content .popup-info li span {
  font-weight: normal;
}
.portfolio .portfolio__content .popup-info li a {
  text-transform: lowercase;
  color: var(--skin-color);
}

.contact-title {
  color: var(--skin-color);
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 2rem;
}

.contact-sub-title {
  color: var(--text-black-900);
  text-align: center;
  font-size: 1.5rem;
  margin-bottom: 6rem;
}

.contact .contact-info-item {
  flex: 0 0 25%;
  max-width: 25%;
  text-align: center;
  margin-bottom: 6rem;
}
.contact .contact-info-item .icon {
  display: inline-block;
}
.contact .contact-info-item .icon .fa {
  font-size: 2.5rem;
  color: var(--skin-color);
}
.contact .contact-info-item h4 {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--text-black-900);
  text-transform: capitalize;
  margin: 1.5rem 0 0.5rem;
}
.contact .contact-info-item p {
  font-size: 1.6rem;
  line-height: 2.5rem;
  color: var(--text-black-700);
  font-weight: 400;
}
.contact .contact-form {
  flex: 0 0 100%;
  max-width: 100%;
}
.contact .contact-form .col-6 {
  flex: 0 0 50%;
  max-width: 50%;
}
.contact .contact-form .col-12 {
  flex: 0 0 100%;
  max-width: 100%;
}
.contact .contact-form .form-item {
  margin-bottom: 3rem;
}
.contact .contact-form .form-item .form-control {
  width: 100%;
  height: 5rem;
  border-radius: 2.5rem;
  background: var(--bg-black-100);
  border: 0.1rem solid var(--bg-black-50);
  padding: 1rem 2.5rem;
  font-size: 1.6rem;
  color: var(--text-black-700);
  transition: all 0.3s ease;
}
.contact .contact-form .form-item .form-control:focus {
  box-shadow: 0 0 2rem rgba(48, 46, 77, 0.15);
}
.contact .contact-form .form-item textarea.form-control {
  height: 14rem;
}
.contact .contact-form .btn {
  height: 5rem;
  padding: 0 5rem;
}
.contact .contact-form .btn:hover {
  cursor: pointer;
}

.style-switcher {
  position: fixed;
  right: 0;
  top: 6rem;
  padding: 1.5rem;
  width: 20rem;
  border: 1px solid var(--bg-black-50);
  background: var(--bg-black-100);
  z-index: 101;
  border-radius: 0.5rem;
  transition: all 0.3s ease;
  transform: translateX(100%);
}
.style-switcher.open {
  transform: translateX(-2.5rem);
}
.style-switcher .s-icon {
  position: absolute;
  height: 4rem;
  width: 4rem;
  text-align: center;
  font-size: 2rem;
  background: var(--bg-black-100);
  color: var(--text-black-900);
  right: 100%;
  border: 1px solid var(--bg-black-50);
  margin-right: 2.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 50%;
}
.style-switcher .s-icon i {
  line-height: 4rem;
}
.style-switcher .style-switcher-toggler {
  top: 0;
}
.style-switcher .day-night {
  top: 5.5rem;
}
.style-switcher h4 {
  margin: 0 0 1rem;
  color: var(--text-black-700);
  font-size: 1.6rem;
  font-weight: 600;
  text-transform: capitalize;
}
.style-switcher .colors {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  cursor: pointer;
}
.style-switcher .colors span {
  display: inline-block;
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
}
.style-switcher .color-1 {
  background: #fa5b0f;
}
.style-switcher .color-2 {
  background: #e4ba00;
}
.style-switcher .color-3 {
  background: #9600d1;
}
.style-switcher .color-4 {
  background: #03b470;
}
.style-switcher .color-5 {
  background: #005ff7;
}

@media (max-width: 1199px) {
  .section {
    left: 0;
  }
  .section .container {
    padding-top: 7rem;
  }
  .section.open {
    left: 27rem;
  }
  .section.open .portfolio__popup.open {
    opacity: 0;
    visibility: hidden;
  }
  .section.open .portfolio__section {
    grid-template-columns: repeat(2, 2fr);
  }
  .row {
    margin-right: 0;
  }
  .aside {
    left: -27rem;
  }
  .aside.open {
    left: 0;
  }
  .aside .nav-toggler {
    display: flex;
    left: 3rem;
  }
  .aside .nav-toggler.open {
    left: 30rem;
  }
  .about .about-content .personal-info .info-item p span {
    display: block;
    margin-left: 0;
  }
}
@media (max-width: 991px) {
  .contact .contact-info-item,
  .service .service-item {
    flex: 0 0 50%;
    max-width: 50%;
  }
  .home.section.open {
    left: 0;
  }
  .home.section.open .btn {
    display: flex;
    position: fixed;
    left: 30rem;
  }
  .home .row {
    justify-content: center;
    align-items: center;
    gap: 4rem;
    flex-direction: column-reverse;
  }
  .home .home-info {
    flex: 0 0 100%;
    max-width: 100%;
  }
  .home .home-img::after {
    right: -2.4rem;
    bottom: -4rem;
  }
  .home .home-img::before {
    left: -2.4rem;
    top: -4rem;
  }
  .home .home-img::after, .home .home-img::before {
    height: 6rem;
    width: 6rem;
  }
  .home .home-img img {
    height: 30rem;
  }
  .portfolio .portfolio-item {
    max-width: 100%;
  }
  .portfolio .portfolio__section {
    grid-template-columns: repeat(2, 2fr);
  }
}
@media (max-width: 767px) {
  .contact .contact-form .col-6,
  .contact .contact-info-item,
  .portfolio .portfolio-item,
  .service .service-item,
  .about .about-content .experience,
  .about .about-content .education,
  .about .about-content .skills,
  .about .about-content .personal-info {
    flex: 0 0 100%;
    max-width: 100%;
  }
  .section.open {
    left: 0;
    direction: unset;
  }
  .section.open .portfolio__section {
    grid-template-columns: unset;
  }
  .aside {
    width: 24rem;
  }
  .aside .nav-toggler.open {
    left: 27rem;
  }
  .home .home-img::after {
    right: -7.2rem;
    bottom: -3rem;
    border-bottom: 0.8rem solid var(--skin-color);
    border-right: 0.8rem solid var(--skin-color);
  }
  .home .home-img::before {
    border-top: 0.8rem solid var(--skin-color);
    border-left: 0.8rem solid var(--skin-color);
  }
  .home .home-img::after, .home .home-img::before {
    height: 6rem;
    width: 6rem;
  }
  .home .home-img img {
    height: 22rem;
  }
  .home.section.open .btn {
    display: block;
    position: unset;
  }
  .btn {
    display: block;
    text-align: center;
  }
  .portfolio .portfolio__section {
    grid-template-columns: repeat(1, 1fr);
  }
  .portfolio .portfolio__content {
    grid-template-columns: unset;
  }
  .portfolio .portfolio__content img {
    margin-bottom: 0.6rem;
  }
}